.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.grid-container {
  width: 350px;
  height: 450px;
  display: grid;
  grid-template-columns: 75px 75px 75px 75px;
  grid-template-rows: 90px 75px 75px 75px 75px;
  align-content: space-evenly;
  justify-content: space-evenly;
  margin: auto;
  background-color: #f4f6ff;
  border-radius: 20px;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
  padding: 15px
}

.grid-item-0 {
  grid-area: 1 / 1 / 2 / 5;
}

.grid-item-1 {
  grid-area: 4 / 1 / 5 / 2;
}

.grid-item-2 {
  grid-area: 4 / 2 / 5 / 3;
}

.grid-item-3 {
  grid-area: 4 / 3 / 5 / 4;
}

.grid-item-4 {
  grid-area: 3 / 1 / 4 / 2;
}

.grid-item-5 {
  grid-area: 3 / 2 / 4 / 3;
}

.grid-item-6 {
  grid-area: 3 / 3 / 4 / 4;
}

.grid-item-7 {
  grid-area: 2 / 1 / 3 / 2;
}

.grid-item-8 {
  grid-area: 2 / 2 / 3 / 3;
}

.grid-item-9 {
  grid-area: 2 / 3 / 3 / 4;
}

.grid-item-10 {
  grid-area: 5 / 1 / 6 / 2;
}

.grid-item-11 {
  grid-area: 2 / 4 / 3 / 5;
}

.grid-item-12 {
  grid-area: 3 / 4 / 4 / 5;
}

.grid-item-13 {
  grid-area: 4 / 4 / 5 / 5;
}

.grid-item-14 {
  grid-area: 5 / 4 / 6 / 5;
}

.grid-item-15 {
  grid-area: 5 / 3 / 6 / 4;
}

.grid-item-16 {
  grid-area: 5 / 2 / 6 / 3;
}

.display {
  border-radius: 10px;
  border-color: grey;
  text-align: right;
  padding-right: 10px;
  margin-bottom: 15px;
  font-size: xx-large;
}

button {
  border-radius: 50%;
  border-color: transparent;
}

button:hover {
  opacity: 0.8;
}

button.numbers {
  background-color: #4f8a8b;
  font-size: x-large;
}

button.operators {
  background-color: #ffcb74;
  font-size: x-large;
}

button.functions {
  background-color: #ea907a;
  font-size: x-large;
}

input {
  background-color: #f4f6ff;
}

